.nav-link-custom{
    padding:0.5rem 2rem;
    background-color: #6b6b6b;
    border-radius:none;
    color:white;
}
.nav-link-custom:hover{
    color:#efb64b;
}
.nav-pills .nav-link{
    border-radius:unset;
}
.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    background-color: #6b6b6b !important;
    color:#efb64b;
}
.para-text{
    color:#4b4b4b;
