}
.box-icon{
    border: 5px solid #ffbf43;
border-radius: 26px;
}
.box-button{
    background-color: transparent;
    border: 1px solid #ffbf43;
    border-radius: 5px;
    font-size: 11px;
}
.box-button:hover{
    color:white;
    background-color:#f98933;
}
.linear-gradient{
    background-image: -webkit-gradient(linear, left top, right top, from(#feb238) , to(#f88832));
    background-image: linear-gradient(to right, #feb238 , #f88832);
    font-size: 26px;
    color: white;
}
.img-leaderdship{
    border-radius: 11px;border: 3px solid #969694;width:200px;margin-top:-134px;
}
