#summary span.collapse:not(.show) {
    /* height: 50px !important;
    overflow: hidden;
    -webkit-line-clamp: 2; */
    display: -webkit-box;
    -webkit-box-orient: vertical;  
}

#summary span.collapsing {
min-height: 42px !important;
}

#summary a.collapsed:after  {
content: '+ Read More';
}

#summary a:not(.collapsed):after {
content: 'close';
}
